import React from 'react'

const PSAs = () => {
    return (
        <div className = "headerDiv bg-black text-center">
        <div className = "container">
            <div className = "pb-2">
                <h1 className = "text-warning">Resources </h1>
                <ul>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://cops.usdoj.gov/ric/Publications/cops-p334-pub.pdf" target="_blank" rel="noreferrer">Preventing and Addressing Bullying and Intolerance: A guide for Law Enforcement</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.glsen.org/research/effectiveness-school-district-anti-bullying-policies" target="_blank" rel="noreferrer">Effectiveness of School District Anti-bullying Policies</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.nea.org/advocating-for-change/new-from-nea/keeping-schools-safe-happy-places-everyone" target="_blank" rel="noreferrer">National Education Association: Keeping Schools Safe, Happy Places for Everyone</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://calschls.org/" target="_blank" rel="noreferrer">California Healthy Kids Survey</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.kidshealth.org/en/kids/bullies.html" target="_blank" rel="noreferrer">Kid’s Health – Dealing with Bullies</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.stopbullying.gov/bullying/special-needs" target="_blank" rel="noreferrer">Bullying and Youth with Disabilities and Special Health Needs</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.stopbullying.gov/resources/research-resources/best-practices-for-state-departments-of-education" target="_blank" rel="noreferrer">Best Practices for State Departments of Education</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://safesupportivelearning.ed.gov/topic-research/safety" target="_blank" rel="noreferrer">National Center on Safe Supportive Learning Environments</a></li>
                    <li className = "my-2 mx-auto list-group-item list-group-item-warning w-50"><a href="https://www.glsen.org/no-name-calling-week" target="_blank" rel="noreferrer">No Name Calling Week – GLSEN</a></li>
                </ul>
            </div>
        </div>
    </div>
    )
}

export default PSAs
